import React, { ChangeEvent, useState } from 'react';
import './app-dropdown.scss';
import { Box, Select, SelectChangeEvent } from '@mui/material';
import MenuItem from '@mui/material/MenuItem';
import { ExpandMore } from '@mui/icons-material';
import { Options } from '../app-popover/popover-component';


interface DropdownProps {
  options: Options[];
  onSelect: (value: string) => void;
}

const Dropdown: React.FC<DropdownProps> = (props: DropdownProps) => {
  const [selectedOption, setSelectedOption] = useState<Options| any>({});

  const handleChange = (event: SelectChangeEvent) => {
    console.log(event.target.value, 'value');

    const value = event.target.value;
    setSelectedOption(value);
  };

  return (
    <>
      <Box sx={{
        flexGrow: 1,
        backgroundColor: "var(--secondary-color)",
        borderRadius: 4
      }}>
        <Select
          className='select'
          classes={{ root: 'roots', outlined: 'outline' }}
          value={selectedOption?.text}
          onChange={handleChange}
          IconComponent={ExpandMore} 
          renderValue={(selected) => {
            if (selected.length === 0) {
              return <em>Placeholder</em>;
            }

            return selected;
          }}
          variant="outlined">
          {
            props.options.map((option: any, index: number) => <MenuItem key={index} value={option.text}>{option.text}</MenuItem>)
          }
        </Select>
      </Box>
    </>
  );
};

export default Dropdown;
